import React, { Component } from 'react';
import Project from './project';
import About from './about';
import Contact from './contact';
import Intro from './intro';
import './app.sass';

class App extends Component {
	constructor(props) {
		super(props)
	
		this.state = {
			 terms: ["about", "contact", "projects"],
			 rt: ["about"],
			 t: [],
			 s: '',
			 r: [<Intro/>]
		}
	}

	s = (s) => {
		let t = [];
		s = s.target.value;
		this.state.terms.forEach( e => {
			if( e.includes(s) ) {
				t.push(e);
			}
		})
		console.log(t);
		
		this.setState({t, s});
	}
	
	goto = (e) => {
		if(e.key == "Enter" && this.state.t.length == 1) {
			if(this.state.t[0] == "projects") { 
				this.setState({r: [...this.state.r, <div style={{width: 1200}}><Project/></div>], s: '', t: []}, () => window.scrollTo( 0, document.body.scrollHeight))
			}
			else if(this.state.t[0] == "about") { 
				this.setState({r: [...this.state.r, <div style={{width: 1200}}><About/></div>], s: '', t: []}, () => window.scrollTo( 0, document.body.scrollHeight))
			}
			else if(this.state.t[0] == "contact") { 
				this.setState({r: [...this.state.r, <div style={{width: 1200}}><Contact/></div>], s: '', t: []}, () => window.scrollTo( 0, document.body.scrollHeight))
			}
			else {
				console.log("Uh oh");
			}
		}
	}

  render() {
    return (
      <div style={{width: "100%", padding: 20, display: "flex", flexDirection: "column", alignItems:"center"}}>
				{this.state.r}
				<div style={{width: "100%", padding: 20, display: "flex", justifyContent: "center"}}>
					<input type="text" placeholder="Enter a command" className="cli" value={this.state.s} onChange={this.s} onKeyUp={this.goto}/>
				</div>
			</div>
    );
  }
}

export default App;
